
import React, { Component } from 'react';
import { useAuth0 } from "@auth0/auth0-react";
import {
  Row,
  Col,
  Input,
  Button,
  Alert,
  Container,
  Label,
  FormGroup,
} from 'reactstrap';

// Redux
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';

// availity-reactstrap-validation
import { AvForm, AvField } from 'availity-reactstrap-validation';

import CheckAuth from './CheckAuth';
// actions
import { checkLogin, apiError } from '../../store/actions';
import LoginButton from './LoginButton';
// import images
import logodark from '../../assets/images/logo-dark.png';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = { username: 'admin@saadabot.com', password: '123456' };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(event, values) {
    this.props.checkLogin(values, this.props.history);
  }
componentDidUpdate(){
  if(window.location.pathname=='/success'){
    //  
       this.props.history.push('/dashboard');
       window.location.reload(true);
}
}
  componentDidMount() {
    if (window.location.pathname == '/success') {
      //  
      this.props.history.push('/dashboard');
      window.location.reload(true);
    }
    this.props.apiError('');
    document.body.classList.add('auth-body-bg');
  }

  componentWillUnmount() {
    document.body.classList.remove('auth-body-bg');

  }
  loginWithRedirect = () => {
    useAuth0();
  }
  render() {

    const AuthNav = () => {
      const { isAuthenticated,user } = useAuth0();

      return (

        <div>
          {isAuthenticated &&  <div>
                                <img src={user.picture} alt={user.name} style={{width:100,height:100}}/>
                                <h2>{user.name}</h2>
                                <p>{user.email}</p>

                              </div>}
          {isAuthenticated ? <Button
            color="primary"
            className="w-md waves-effect waves-light"
          // type="submit"
            onClick={() => {
              this.props.history.push('/success')
              // this.handleSubmit()
              
              }}
          >
            Dashboard
                                </Button> : <LoginButton/>}

        </div>

      );
    };
    return (
      <React.Fragment>
        <div className="home-btn d-none d-sm-block">
          <Link to="/">
            <i className="mdi mdi-home-variant h2 text-white"></i>
          </Link>
        </div>

        <div>
          <Container fluid className="p-0">
            <Row className="no-gutters">
              <Col lg={4}>
                <div className="authentication-page-content p-4 d-flex align-items-center min-vh-100">
                  <div className="w-100">
                    <Row className="justify-content-center">
                      <Col lg={9}>
                        <div>
                          <div className="text-center">
                            <div>
                              <Link to="/" className="logo">
                                <img src={logodark} height="20" alt="logo" />
                              </Link>
                            </div>

                            <h4 className="font-size-18 mt-4">
                              Welcome Back !
                            </h4>
                          </div>

                          {this.props.loginError && this.props.loginError ? (
                            <Alert color="danger">
                              {this.props.loginError}
                            </Alert>
                          ) : null}

                          <div className="p-2 mt-5">
                            <AvForm
                              className="form-horizontal"
                              onValidSubmit={this.handleSubmit}
                            >
                              <FormGroup className="auth-form-group-custom mb-4" style={{ display: 'none' }}> 
                                <i className="ri-user-2-line auti-custom-input-icon"></i>
                                <Label htmlFor="username">Username</Label>
                                <AvField
                                  name="username"
                                  value={this.state.username}
                                  type="text"
                                  className="form-control"
                                  id="username"
                                  validate={{ email: true, required: true }}
                                  placeholder="Enter username"
                                />
                              </FormGroup>

                              <FormGroup className="auth-form-group-custom mb-4" style={{ display: 'none' }}> 
                                <i className="ri-lock-2-line auti-custom-input-icon"></i>
                                <Label htmlFor="userpassword">Password</Label>
                                <AvField
                                  name="password"
                                  value={this.state.password}
                                  type="password"
                                  className="form-control"
                                  id="userpassword"
                                  placeholder="Enter password"
                                />
                              </FormGroup>

                              <div className="custom-control custom-checkbox" style={{display:'none'}}> 
                                <Input
                                  type="checkbox"
                                  className="custom-control-input"
                                  id="customControlInline"
                                />
                                <Label
                                  className="custom-control-label"
                                  htmlFor="customControlInline"
                                >
                                  Remember me
                                </Label>
                              </div>

                              <div className="mt-4 text-center">
                                <AuthNav/>
                                {/* <Button
                                  color="primary"
                                  className="w-md waves-effect waves-light"
                                  type="submit"
                                  onClick={() => this.handleSubmit()}
                                >
                                  Dashboard
                                </Button> */}
                              </div>

                              <div className="mt-4 text-center">
                                <Link
                                  to="/forgot-password"
                                  className="text-muted"
                                >
                                  <i className="mdi mdi-lock mr-1"></i> Forgot
                                  your password?
                                </Link>
                              </div>
                            </AvForm>
                          </div>

                          <div className="mt-5 text-center">
                            <p>
                              Don't have an account ?{' '}
                              <Link
                                to="/register"
                                className="font-weight-medium text-primary"
                              >
                                {' '}
                                Register{' '}
                              </Link>{' '}
                            </p>
                            <p>© 2020 Flowbuilder. A product of Saadabot</p>
                          </div>
                        </div>
                      </Col>
                    </Row>
                  </div>
                </div>
              </Col>
              <Col lg={8}>
                <div className="authentication-bg">
                  {/* <div className="bg-overlay"></div> */}
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    );
  }
}

const mapStatetoProps = (state) => {
  const { loginError } = state.Login;
  return { loginError };
};

export default withRouter(
  connect(mapStatetoProps, { checkLogin, apiError })(Login)
);
